import axios from 'axios'
const state = {
  orderList: [],
  params: {}
}
// stategettersstate
const getters = {
  getOrderList: (state) => state.orderList
}
// 
const mutations = {
  // statethis.$store.commit('changeOrderList', orderListData)mutations
  updataOrderList (state, payLoad) {
    state.orderList = payLoad
  },
  updataParams (state, {key, val}) {
    state.params[key] = val
    console.log(state.params)
  }
}
// 
const actions = {
  // ES6
  // commit mutations
  // statestate
  fetchOrderList ({commit, state}) {
    axios.post('https://easy-mock.com/mock/5a27a82a7f2b435f137cfcc1/example/getOrderList', state.params).then((res) => {
      commit('updataOrderList', res.data.getOrderList.list)
    }).catch((err) => {
      console.log(err)
    })
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



