// 
<template>
  <div class="sales-board">
      <div class="sales-board-intro">
        <h2></h2>
        <p></p>
      </div>
      <div class="sales-board-form">
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                <keep-alive>
                  <v-counter :min="20" :max="100" @on-change="onParamChange('buyNum',$event)"></v-counter>
                </keep-alive>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                <keep-alive>
                  <v-selection :selections="productTypes" @on-change="onParamChange('buyType', $event)"></v-selection>
                </keep-alive>

              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                <keep-alive>
                  <v-chooser :selections="periodList" @on-change="onParamChange('period', $event)"></v-chooser>
                </keep-alive>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                <keep-alive>
                  <v-chooser :selections="versionList" @on-change="onParamChange('versions', $event)"></v-chooser>
                </keep-alive>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  {{ price }}
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">&nbsp;</div>
              <div class="sales-board-line-right">
                  <div class="button" @click="showPayDialog">
                    
                  </div>
              </div>
          </div>
      </div>
      <div class="sales-board-des">
        <h2></h2>
        <p></p>

        <h3></h3>
        <ul>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>

        <h3></h3>
        <ul>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </div>
      <my-dialog :is-show="isShowPayDialog" @close="hidePayDialog">
        <table class="buy-dialog-table">
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          <tr>
            <td>{{ buyNum }}</td>
            <td>{{ buyType.label }}</td>
            <td>{{ period.label }}</td>
            <td>
              <span>{{ versions.label }}</span>
            </td>
            <td>{{ price }}</td>
          </tr>
        </table>
        <h3 class="buy-dialog-title"></h3>
        <bank-chooser @on-change="onChangeBanks"></bank-chooser>
        <div class="button buy-dialog-btn" @click="confirmBuy">
          
        </div>
      </my-dialog>
      <my-dialog :is-show="isShowErrDialog" @close="hideErrDialog">
        
      </my-dialog>
      <check-order :is-show-check-dialog="isShowCheckOrder" :order-id="orderId" @on-close-check-dialog="hideCheckOrder"></check-order>
  </div>
</template>

<script>
import MyDialog from '@/components/base/dialog'
import VSelection from '@/components/base/selection'
import VCounter from '@/components/base/counter'
import VChooser from '@/components/base/chooser'
import BankChooser from '@/components/bankChooser'
import CheckOrder from '@/components/checkOrder'
export default {
  data () {
    return {
      buyNum: 0,
      buyType: {},
      versions: {},
      period: {},
      productTypes: [{label: 'test', value: 0}],
      versionList: [{label: 'test', value: 0}],
      periodList: [{label: 'test', value: 0}],
      price: 0,
      isShowPayDialog: false,
      bankId: null,
      orderId: null,
      isShowCheckOrder: false,
      isShowErrDialog: false
    }
  },
  components: {
    MyDialog,
    VSelection,
    VCounter,
    VChooser,
    BankChooser,
    CheckOrder
  },
  methods: {
    onParamChange (attr, val) {
      this[attr] = val
      this.getPrice()
    },
    getPrice () {
      let passParams = {
        buyNumber: this.buyNum,
        buyType: this.buyType.value,
        period: this.period.value,
        version: this.versions
      }
      this.$axios.post('https://easy-mock.com/mock/5a27a82a7f2b435f137cfcc1/example/getPrice', passParams).then((res) => {
        this.price = res.data.getPrice.amount
      }).catch((err) => {
        console.log(err)
      })
    },
    showPayDialog () {
      if (this.$store.getters.getLoginStatus) {
        this.isShowPayDialog = true
      } else {
        alert('~')
      }
    },
    hidePayDialog () {
      this.isShowPayDialog = false
    },
    hideErrDialog () {
      this.isShowErrDialog = false
    },
    hideCheckOrder () {
      this.isShowCheckOrder = false
    },
    onChangeBanks (bankObj) {
      this.bankId = bankObj.id
    },
    confirmBuy () {
      let passParams = {
        buyNumber: this.buyNum,
        buyType: this.buyType.value,
        period: this.period.value,
        version: this.versions,
        bankId: this.bankId
      }
      this.$axios.get('https://easy-mock.com/mock/5a27a82a7f2b435f137cfcc1/example/bank', passParams).then((res) => {
        console.log('')
        this.orderId = res.data.createOrder.orderId
        this.isShowCheckOrder = true
        this.isShowPayDialog = false
      }).catch((err) => {
        this.isShowErrDialog = true
        console.log(err)
      })
    }
  },
  mounted () {
    this.$axios.get('https://easy-mock.com/mock/5a27a82a7f2b435f137cfcc1/example/originalPrice').then((res) => {
      this.price = res.data.getPrice.amount
    })
    this.$axios.get('https://easy-mock.com/mock/5a27a82a7f2b435f137cfcc1/example/analysis').then((res) => {
      this.productTypes = res.data.buyTypes
      this.versionList = res.data.versionList
      this.periodList = res.data.periodList
    }).then(() => {
      this.buyNun = 1
      this.buyType = this.productTypes[0]
      this.versions = this.versionList[0]
      this.period = this.periodList[0]
    }).catch((error) => {
      console.log(error)
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .buy-dialog-title {
    font-size: 16px;
    font-weight: bold;
  }
  .buy-dialog-btn {
    display: block;
    margin: 20px auto;
    width: 100px;
    text-align: center;
  }
  .buy-dialog-table {
    width: 100%;
    margin-bottom: 20px;
  }
  .buy-dialog-table td,
  .buy-dialog-table th{
    border: 1px solid #e3e3e3;
    text-align: center;
    padding: 5px 0;
  }
  .buy-dialog-table th {
    background: #333333;
    color: #fff;
    border: 1px solid #333333;
  }
</style>



