// 
<template>
  <div class="sales-board">
      <div class="sales-board-intro">
        <h2></h2>
        <p></p>
      </div>
      <div class="sales-board-form">
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  <v-counter :max="100" :min="20" @on-change="onParamChange('buyNum',$event)"></v-counter>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  <v-mul-chooser :selections="dataList" @on-change="onParamChange('medium', $event)"></v-mul-chooser>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  {{ price }} 
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">&nbsp;</div>
              <div class="sales-board-line-right">
                  <div class="button" @click="showPayDialog">
                    
                  </div>
              </div>
          </div>
      </div>
      <div class="sales-board-des">
        <h2></h2>
        <p>2020



</p>
      </div>
      <my-dialog :is-show="isShowPayDialog" @close="hidePayDialog">
        <table class="buy-dialog-table">
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          <tr>
            <td>{{ buyNum }}</td>
            <td>{{ dataList[0].label }}</td>
            <td></td>
            <td>{{ price }}</td>
          </tr>
        </table>
        <h3 class="buy-dialog-title"></h3>
        <bank-chooser @on-change="onChangeBanks"></bank-chooser>
        <div class="button buy-dialog-btn" @click="confirmBuy">
          
        </div>
      </my-dialog>
      <my-dialog :is-show="isShowErrDialog" @close="hideErrDialog">
        
      </my-dialog>
      <check-order :is-show-check-dialog="isShowCheckOrder" :order-id="orderId" @on-close-check-dialog="hideCheckOrder"></check-order>
  </div>
</template>

<script>
import VMulChooser from '@/components/base/multiplyChooser'
import VCounter from '@/components/base/counter'
import MyDialog from '@/components/base/dialog'
import BankChooser from '@/components/bankChooser'
import CheckOrder from '@/components/checkOrder'

export default {
  data () {
    return {
      buyNum: 0,
      medium: {label: 'test', value: 0},
      dataList: [{label: 'test', value: 0}],
      price: 0,
      isShowPayDialog: false,
      bankId: null,
      orderId: null,
      isShowCheckOrder: false,
      isShowErrDialog: false
    }
  },
  components: {
    VMulChooser,
    VCounter,
    BankChooser,
    CheckOrder,
    MyDialog
  },
  methods: {
    // 
    onParamChange (attr, val) {
      this[attr] = val
      console.log(this[attr])
      console.log(val)
      this.getPrice()
    },
    getPrice () {
      let passParams = {
        buyNumber: this.buyNum,
        medium: this.medium
      }
      this.$axios.post('https://easy-mock.com/mock/5a27a82a7f2b435f137cfcc1/example/getPrice', passParams).then((res) => {
        this.price = res.data.getPrice.amount
      }).catch((err) => {
        console.log(err)
      })
    },
    showPayDialog () {
      if (this.$store.getters.getLoginStatus) {
        this.isShowPayDialog = true
      } else {
        alert('~')
      }
    },
    hidePayDialog () {
      this.isShowPayDialog = false
    },
    hideErrDialog () {
      this.isShowErrDialog = false
    },
    hideCheckOrder () {
      this.isShowCheckOrder = false
    },
    onChangeBanks (bankObj) {
      this.bankId = bankObj.id
    },
    confirmBuy () {
      let passParams = {
        buyNumber: this.buyNum,
        medium: this.medium,
        bankId: this.bankId
      }
      this.$axios.get('https://easy-mock.com/mock/5a27a82a7f2b435f137cfcc1/example/bank', passParams).then((res) => {
        console.log('')
        this.orderId = res.data.createOrder.orderId
        this.isShowCheckOrder = true
        this.isShowPayDialog = false
      }).catch((err) => {
        this.isShowErrDialog = true
        console.log(err)
      })
    }
  },
  mounted () {
    this.$axios.get('https://easy-mock.com/mock/5a27a82a7f2b435f137cfcc1/example/originalPrice').then((res) => {
      this.price = res.data.getPrice.amount
    })
    this.$axios.get('https://easy-mock.com/mock/5a27a82a7f2b435f137cfcc1/example/analysis').then((res) => {
      this.dataList = res.data.dataTypes
      this.medium = this.dataList[0]
      this.buyNum = 20
    }).catch((err) => {
      console.log(err)
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .buy-dialog-title {
    font-size: 16px;
    font-weight: bold;
  }
  .buy-dialog-btn {
    display: block;
    margin: 20px auto;
    width: 100px;
    text-align: center;
  }
  .buy-dialog-table {
    width: 100%;
    margin-bottom: 20px;
  }
  .buy-dialog-table td,
  .buy-dialog-table th{
    border: 1px solid #e3e3e3;
    text-align: center;
    padding: 5px 0;
  }
  .buy-dialog-table th {
    background: #333333;
    color: #fff;
    border: 1px solid #333333;
  }
</style>



